nav {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    width: 100%;
    height: fit-content;
    background: #333333;
}
nav a {
    margin: 10px 25px;
    text-decoration: none;
    color: #999999;
}
nav a:hover {
    letter-spacing: 2px;
    color: #cccccc;
}